import React from 'react'
import makeStyles from '@material-ui/core/styles/makeStyles'
import styles from './Codelist.styles'

import { fetchCodelist, moveCodeUpward, moveCodeDownward, ICode } from '../../../codes/services/CodesService'
import { useHistory, useRouteMatch } from 'react-router-dom'

import HeaderTitle from '../../../../components/Header/HeaderTitle'
import LinearProgress from '@material-ui/core/LinearProgress'
import List from '@material-ui/core/List'
import CodelistItem from '../CodelistItem/CodelistItem'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'

import CreateItem from '../CodelistTool/CreateItem/CreateItem'
import UpdateItem from '../CodelistTool/UpdateItem/UpdateItem'
import DeleteItem from '../CodelistTool/DeleteItem/DeleteItem'

import { Switch, Route, NavLink } from 'react-router-dom'

const useStyles = makeStyles(styles, {
    name: Codelist.name,
})

export default function Codelist() {
    const classes = useStyles()

    const [ codelist, setCodelist ] = React.useState<ICode[] | null>(null)
    const [ codelistPending, setCodelistPending ] = React.useState(true)

    React.useEffect(() => {
        if (!codelistPending) {
            return
        }
        let active = true
        fetchCodelist({})
            .then((codelist) => {
                if (active) {
                    setCodelist(codelist)
                    setCodelistPending(false)
                }
            })
        ;
        return () => {
            active = false
        }
    }, [codelistPending])

    const history = useHistory()
    const routeMatch = useRouteMatch()

    return (
        <>

            <HeaderTitle>
                Сертификаты о вакцинации
            </HeaderTitle>

            {codelistPending && (
                <div className={classes.progressContainer}>
                    <LinearProgress className={classes.progress} color="secondary"/>
                </div>
            )}

            <div className={classes.main}>
                {codelist && (
                    <List>
                        {codelist.map((item) => (
                            <CodelistItem
                                key={item.id}
                                item={item}
                                onMoveUpward={() => {
                                    moveCodeUpward(item.id)
                                        .then(() => {
                                            setCodelistPending(true)
                                        })
                                    ;
                                }}
                                onMoveDownward={() => {
                                    moveCodeDownward(item.id)
                                        .then(() => {
                                            setCodelistPending(true)
                                        })
                                    ;
                                }}
                            />
                        ))}
                    </List>
                )}
            </div>

            <div className={classes.foot}>
                <Fab
                    color="primary"
                    component={NavLink}
                    to={`${routeMatch.url}/create`}
                >
                    <AddIcon/>
                </Fab>
            </div>

            <Switch>

                <Route path={`${routeMatch.path}/create`}>
                    <CreateItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

                <Route path={`${routeMatch.path}/:id/update`}>
                    <UpdateItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

                <Route path={`${routeMatch.path}/:id/delete`}>
                    <DeleteItem
                        onClose={() => {
                            history.goBack()
                        }}
                        onSuccess={() => {
                            history.goBack()
                            setCodelistPending(true)
                        }}
                    />
                </Route>

            </Switch>

        </>
    )
}
