import React from 'react'

export interface IHeaderContextValue {
    titleElementRef: React.RefObject<HTMLDivElement>
}

export const HeaderContext = React.createContext<IHeaderContextValue | null>(null)

export interface IHeaderContextProviderProps {
    children: React.ReactNode
}

export default function HeaderContextProvider(props: IHeaderContextProviderProps) {
    const { children } = props

    //const [ titleElement, setTitleElement ] = React.useState()

    const titleElementRef = React.useRef<HTMLDivElement>(null)

    const value = React.useMemo<IHeaderContextValue>(() => {
        return {
            titleElementRef,
        }
    }, [
        titleElementRef,
    ])

    return (
        <HeaderContext.Provider value={value}>
            {children}
        </HeaderContext.Provider>
    )
}
