import React from 'react'
import makeStyles from '@material-ui/core/styles/makeStyles'
import styles from './CodelistItem.styles'
import clsx from 'clsx'

import { ICode } from '../../../codes/services/CodesService'
import { useHistory, useRouteMatch } from 'react-router-dom'

import { NavLink } from 'react-router-dom'
import ListItem from '@material-ui/core/ListItem'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward'
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward'
import EditIcon from '@material-ui/icons/Edit'
import DeleteForeverIcon from '@material-ui/icons/DeleteForever'

import { compareAsc } from 'date-fns'

const useStyles = makeStyles(styles, {
    name: CodelistItem.name,
})

export interface ICodelistItemProps {
    item: ICode
    onMoveUpward: () => void
    onMoveDownward: () => void
}

export default function CodelistItem(props: ICodelistItemProps) {
    const classes = useStyles(props)

    const { item, onMoveUpward, onMoveDownward } = props

    const [ menuOpen, setMenuOpen ] = React.useState(false)
    const menuAnchorRef = React.useRef<HTMLButtonElement>(null)

    const routeMatch = useRouteMatch()

    const status = React.useMemo(() => {
        if (item.expiredAt) {
            if (compareAsc(new Date(item.expiredAt), new Date) > 0) {
                return (
                    <Typography className={clsx(classes.status, classes.status_valid)} variant="body2" component="span">
                        Действителен
                    </Typography>
                )
            } else {
                return (
                    <Typography className={clsx(classes.status, classes.status_invalid)} variant="body2" component="span">
                        Просрочен
                    </Typography>
                )
            }
        } else {
            return (
                <Typography className={clsx(classes.status)} color="textSecondary" variant="body2" component="span">
                    Срок действия не указан
                </Typography>
            )
        }
    }, [item])

    return (
        <>

            <ListItem
                className={classes.item}
                button
                component={NavLink}
                to={`/codes/code/${item.id}`}
            >
                {/*<ListItemIcon className={classes.itemIcon}>*/}
                {/*    <img className={classes.itemIconImage} src={item.imageUrl}/>*/}
                {/*</ListItemIcon>*/}
                <ListItemText
                    disableTypography
                    primary={(
                        <div className={classes.itemName}>
                            <Typography variant="body1" color="textPrimary" component="span">{item.name}</Typography> <Typography variant="body1" color="textSecondary" component="span">{item.url || ' '}</Typography>
                        </div>
                    )}
                    secondary={(
                        <div className={classes.itemDesc}>
                            {status}
                        </div>
                    )}
                />
                <ListItemSecondaryAction>
                    <IconButton
                        ref={menuAnchorRef}
                        aria-label="comments"
                        edge="end"
                        onClick={() => setMenuOpen(true)}
                    >
                        <MoreVertIcon/>
                    </IconButton>
                </ListItemSecondaryAction>
            </ListItem>

            {menuAnchorRef.current && (
                <Menu
                    anchorEl={menuAnchorRef.current}
                    open={menuOpen}
                    onClose={() => setMenuOpen(false)}
                >

                    <MenuItem
                        onClick={() => { setMenuOpen(false); onMoveUpward() }}
                    >
                        <ListItemIcon>
                            <ArrowUpwardIcon/>
                        </ListItemIcon>
                        <ListItemText
                            primary="Переместить выше"
                        />
                    </MenuItem>

                    <MenuItem
                        onClick={() => { setMenuOpen(false); onMoveDownward() }}
                    >
                        <ListItemIcon>
                            <ArrowDownwardIcon/>
                        </ListItemIcon>
                        <ListItemText
                            primary="Переместить ниже"
                        />
                    </MenuItem>

                    <MenuItem
                        component={NavLink}
                        to={`${routeMatch.path}/${item.id}/update`}
                        onClick={() => setMenuOpen(false)}
                    >
                        <ListItemIcon>
                            <EditIcon/>
                        </ListItemIcon>
                        <ListItemText
                            primary="Изменить"
                        />
                    </MenuItem>

                    <MenuItem
                        component={NavLink}
                        to={`${routeMatch.path}/${item.id}/delete`}
                        onClick={() => setMenuOpen(false)}
                    >
                        <ListItemIcon>
                            <DeleteForeverIcon/>
                        </ListItemIcon>
                        <ListItemText
                            primary="Удалить"
                        />
                    </MenuItem>

                </Menu>
            )}

        </>
    )
}
