import React from 'react'
import makeStyles from '@material-ui/core/styles/makeStyles'
import styles from './Code.styles'

import { useParams } from 'react-router-dom'
import { fetchCode, fetchCodelist, putCurrentCodeId, ICode } from '../../../codes/services/CodesService'

import HeaderTitle from '../../../../components/Header/HeaderTitle'
import Toolbar from '@material-ui/core/Toolbar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { NavLink } from 'react-router-dom'

const useStyles = makeStyles(styles, {
    name: Code.name,
})

export default function Code() {
    const classes = useStyles()

    const { id } = useParams<{ id:string }>()

    React.useEffect(() => {
        putCurrentCodeId(id)
    }, [id])

    const [ code, setCode ] = React.useState<ICode | null>()
    const [ codePending, setCodePending ] = React.useState(true)

    React.useEffect(() => {
        setCodePending(true)
        fetchCode(id)
            .then((code) => {
                setCode(code)
                setCodePending(false)
            })
        ;
    }, [id])

    const [ codelist, setCodelist ] = React.useState<ICode[] | null>(null)
    const [ codelistPending, setCodelistPending ] = React.useState(true)

    React.useEffect(() => {
        if (!codelistPending) {
            return
        }
        let active = true
        fetchCodelist({})
            .then((codelist) => {
                if (active) {
                    setCodelist(codelist)
                    setCodelistPending(false)
                }
            })
        ;
        return () => {
            active = false
        }
    }, [codelistPending])

    return (
        <>

            {/*<AppBar position="sticky" color="transparent" elevation={0}>*/}
            {/*    <Container maxWidth={false}>*/}
            {/*        <Toolbar disableGutters>*/}
            {/*            COVIDCODE*/}
            {/*        </Toolbar>*/}
            {/*    </Container>*/}
            {/*</AppBar>*/}

            <HeaderTitle>
                Сертификат о вакцинации
            </HeaderTitle>

            {codePending && (
                <div>...</div>
            )}

            {code && (
                <main className={classes.main}>

                    {code.imageUrl && (
                        <img className={classes.mainImage} src={code.imageUrl} alt=""/>
                    )}

                </main>
            )}

            <div className={classes.foot}>
                <Toolbar className={classes.footToolbar} disableGutters>
                    {codelist && codelist.length > 1 && (
                        <Tabs
                            className={classes.footTabs}
                            variant="scrollable"
                            value={id}
                        >
                            {codelist.map((code) => (
                                <Tab
                                    key={code.id}
                                    className={classes.footTab}
                                    label={code.name}
                                    value={code.id}
                                    component={NavLink}
                                    to={`/codes/code/${code.id}`}
                                    replace
                                />
                            ))}
                        </Tabs>
                    )}
                </Toolbar>
            </div>

        </>
    )
}
